.hidden {
  display: none;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@keyframes show {
  from {
    opacity: 0;
    transform: translateX(-5rem);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

html {
  min-width: auto;
  text-align: center;
  font-family: Nunito Sans, sans-serif;
  font-weight: 300;
}

body {
  color: #f1edea;
  grid-template-columns: 1fr 3fr;
  align-items: center;
  display: grid;
}

nav, .article {
  min-height: 100vh;
  background-color: #161d30;
  border-right: 2rem solid #232038;
  gap: 1rem;
  padding: 2rem;
}

span {
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  display: flex;
}

span a, select, button {
  text-align: center;
  color: #fff;
  width: 100%;
  background-color: #ffffff1a;
  border-radius: 2rem;
  padding: 1rem;
  text-decoration: none;
}

nav a {
  background-color: #80f;
}

select:hover {
  cursor: pointer;
  background: #161d30;
}

button:hover {
  cursor: pointer;
  background: #161d30;
  border: 1px solid #978989;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

p {
  margin: 2rem 0;
}

main {
  animation: .3s show;
}

main a {
  min-width: 20rem;
  color: #fff;
  background-color: #80f;
  border-radius: 2rem;
  padding: 1rem;
  font-weight: 900;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 5px #80f6, 0 10px #8800ff4d, 0 15px #80f3, 0 20px #8800ff1a, 0 25px #8800ff0d;
}

main a:hover {
  background-color: #50a;
}

.svg {
  width: 50%;
}

.rythme37 {
  animation: 10s linear infinite planet37;
}

.rythme46 {
  animation: 10s linear infinite planet46;
}

.rythme55 {
  animation: 10s linear infinite planet55;
}

.rythme64 {
  animation: 10s linear infinite planet64;
}

@keyframes planet37 {
  0% {
    transform: scale(.5);
  }

  70% {
    transform: scale(.8);
  }

  100% {
    transform: scale(.5);
  }
}

@keyframes planet46 {
  0% {
    transform: scale(.5);
  }

  40% {
    transform: scale(.8);
  }

  100% {
    transform: scale(.5);
  }
}

@keyframes planet55 {
  0% {
    transform: scale(.5);
  }

  50% {
    transform: scale(.8);
  }

  100% {
    transform: scale(.5);
  }
}

@keyframes planet64 {
  0% {
    transform: scale(.5);
  }

  60% {
    transform: scale(.8);
  }

  100% {
    transform: scale(.5);
  }
}

html {
  background: radial-gradient(at bottom, #1b2735 0%, #090a0f 100%);
  overflow: hidden;
}

